import { mapState, mapMutations } from 'vuex';
import {devices} from '@/api/device';
export default {
	data() {
		return {
			quality:[
				"优","良","轻度污染","中度污染","重度污染","严重污染"
			]
		}
	},
	computed:{
		...mapState(["currentAddress", "devices", "currentUser"])
	},
	onLoad() {
		console.log("onLoad")
		uni.setNavigationBarTitle({
		　　title:'智能硬件'
		})
		
	},
	onHide(){
		console.log("onHide")
	},
	onShow(){
		devices(this.currentUser.OpenId).then(res=>{
			if(res.data.data && res.data.data.length > 0){
				this.$store.commit("setDevices", res.data.data)
			} else {
				this.$store.commit("setDevices", [])
			}
		})
	},
	mounted(){
		console.log("mounted")
	},
	created(){
		console.log("created")
	},
	beforeCreate(){
		console.log("beforeCreate")
	},
	beforeMount(){
		console.log("beforeMount")
	},
	destroyed(){
		console.log("destroyed")
	},
	methods: {
		shortName(name){
			if(name && name.length > 4){
				return name.substr(0, 4) + "...";
			}
			return name;
		},
		...mapMutations(["setSelectDevice", "setDevices"]),
		switchAddress(){
			uni.navigateTo({
				url:'../address/addressList'
			})
		},
		addDevice(){
			uni.navigateTo({
				url:'./addDevice'
			})
		},
		selectDevice(device){
			this.$store.commit("setSelectDevice", device);
			uni.navigateTo({
				url:'./deviceDetail'
			})
		},
		configDevice(device){
			this.$store.commit("setSelectDevice", device);
			uni.navigateTo({
				url:`./configDevice?id=${device.Id}`
			})
		}
	}
}
